.git{
    color:white;
    display: flex;
    justify-content: center;
    margin-top:-50px;
}
.stat{
    display: flex;
    margin:auto;
    justify-content: center;
    width:80%;
    margin-top: 10px;
}
	
@media screen and (max-width: 600px) {
	.stat {
		flex-direction: column;
	}
}
@media screen and (max-width: 1100px) {
	.experiences {
		width: 85vw;
	}
	.container {
		width: 70%;
	}
}
@media screen and (max-width: 900px) {
	.experiences {
		width: 95vw;
	}
	.container {
		width: 80%;
	}
}
